// Checkbox and radio button shared styles
%pnp-shared-input-checkbox-radio-button-styles {
    position: absolute;
    width: 0;
    height: 0;
    cursor: pointer;
    opacity: 0;

    + label {
        @include pnp-typeface(text-18, true);
        position: relative;
        display: inline-flex;
        align-items: center;
        margin-left: 10px;
        cursor: pointer;
        user-select: none;

        &::before {
            position: relative;
            display: block;
            flex-shrink: 0;
            box-sizing: border-box;
            width: 20px;
            height: 20px;
            margin-right: 8px;
            background-color: $grey-10;
            border: 1px solid $grey-40;
            transition: border-color 0.3s ease-in-out;
            content: '';
            @media (prefers-reduced-motion: reduce) {
                transition: none;
            }
        }

        &::after {
            position: absolute;
            display: none;
            border-color: $grey-40;
            border-style: solid;
            transition: border-color 0.3s ease-in-out, background-color 0.3s ease-in-out;
            content: '';
            @media (prefers-reduced-motion: reduce) {
                transition: none;
            }
        }
    }

    &:checked {
        + label {
            &::before {
                border-color: $grey-70;
            }

            &::after {
                display: block;
                border-color: $grey-70;
            }
        }

        &:hover,
        &:focus {
            + label {
                &::before {
                    border-color: $black;
                }

                &::after {
                    display: block;
                    border-color: $black;
                }
            }
        }
    }

    &:disabled {
        + label {
            color: $grey-70;

            &::before {
                background-color: $grey-10;
                border-color: $grey-20;
            }

            &::after {
                border-color: $grey-40;
            }
        }

        &:hover,
        &:focus {
            + label {
                color: $grey-70;

                &::before {
                    background-color: $grey-10;
                    border-color: $grey-20;
                }

                &::after {
                    border-color: $grey-40;
                }
            }
        }
    }

    &:hover,
    &:focus {
        + label {
            &::before {
                background-color: transparent;
                border-color: $black;
            }
        }
    }

    &:focus {
        + label {
            outline: 2px solid $utility-focus;
            outline-offset: 2px;
        }
    }
}

/**
 * Example markup
 * <input type="checkbox" checked="checked">
 * <label class="checkbox-container">One
 * </label>
 */
@mixin pnp-custom-checkbox {
    @extend %pnp-shared-input-checkbox-radio-button-styles;

    + label {
        &::after {
            top: calc(50% - 6px);
            left: 7px;
            width: 5px;
            height: 10px;
            border-width: 0 1px 1px 0;
            transform: rotate(45deg);
        }
    }
}

/**
 * Example markup
 * <input type="checkbox" checked="checked">
 * <label class="radio-container">One
 * </label>
 */
@mixin pnp-custom-radio-button {
    @extend %pnp-shared-input-checkbox-radio-button-styles;

    + label {
        &::before {
            border-color: $grey-70;
            border-radius: 50%;
        }

        &::after {
            left: 5px;
            width: 10px;
            height: 10px;
            border-radius: 50%;
        }
    }

    &:checked {
        + label {
            &::after {
                background-color: $grey-70;
            }
        }

        &:hover,
        &:focus {
            + label {
                &::after {
                    background-color: $black;
                }
            }
        }

        &:disabled {
            + label {
                &::after {
                    background-color: $grey-40;
                }
            }

            &:hover,
            &:focus {
                + label {
                    background-color: $grey-40;
                }
            }
        }
    }
}

@mixin pnp-custom-input {
    display: block;
    width: 100%;
    padding: 8px 42px 8px 8px;
    color: $black;
    line-height: 30px;
    border: 1px solid $grey-80;
    border-radius: $rounded-corner-radius;

    &:focus,
    &:focus-within,
    &:focus-visible {
        outline: -1px solid $utility-focus;
    }

    &:disabled {
        background: $grey-10;
    }

    &[type='file'] {
        padding: 0;
        border: 0;

        &:focus {
            outline-offset: 2px;
        }

        &::file-selector-button {
            @include pnp-button;
            margin-right: 8px;
        }
    }
}

@mixin pnp-custom-label {
    @include pnp-typeface(text-16-bold, true);
    display: block;
}

@mixin pnp-custom-note {
    @include pnp-typeface(text-14, true);
    display: block;
    color: $grey-70;
}

@mixin pnp-custom-select {
    &:not([multiple]) {
        position: relative;
        padding-right: 40px;
        background: url('data:image/svg+xml;utf8,<svg width="16" height="10" viewBox="0 0 16 10" fill="none" xmlns="http://www.w3.org/2000/svg"> <path d="M8 9.5001L0.5 2.0001L1.55 0.9501L8 7.4001L14.45 0.9501L15.5 2.0001L8 9.5001Z" fill="black"/> </svg>')
            calc(100% - 12px);
        background-repeat: no-repeat;
        border-radius: $rounded-corner-radius;
        appearance: none;
    }
}
