@import '../../drawer/css/global';

.facet-drawer {
    .drawer {
        &__controllers {
            display: flex;
            justify-content: space-between;
            padding: 0 pxToRem(20);
            color: $white;
            background-color: $black;
        }

        &__icon {
            @include pnp-svg-icon(x-large);
        }

        &__close {
            padding: 0;
            color: $primary-a;
            background-color: transparent;
        }

        &__content {
            .facet {
                max-width: 100%;
                padding: 0;
                border: 0;
            }

            .facet-group {
                padding-right: pxToRem(20);
                padding-bottom: pxToRem(20);
                padding-left: pxToRem(20);
                border-bottom: 1px solid $grey-10;

                &__title {
                    @include pnp-typeface(heading-18-bold);
                    padding: 0;
                    color: $black;
                    background-color: transparent;
                    // Ensure that facet titles are not tabable when drawer is closed.
                    visibility: hidden;
                }

                &__list-item {
                    color: $black;
                    // Ensure that facet categories are not tabable when drawer is closed.
                    visibility: hidden;
                }

                &__show-more {
                    visibility: hidden;
                }

                &__clear {
                    visibility: hidden;
                }
            }

            .facet-groups-controller {
                display: none; // We never want to display this when facets are in a drawer.
            }
        }

        // Make the facet categories if the drawer is open.
        &--is-visible {
            .facet-group__list-item {
                visibility: inherit;

                &--hidden {
                    visibility: hidden;
                }
            }

            // Make all the links tabable.
            .facet-group__title,
            .facet-group__show-more,
            .facet-group__clear {
                visibility: visible;
            }
        }
    }
}
