@mixin pnp-svg-icon($size: medium) {
    display: inline-flex;
    align-items: center;
    justify-content: center;

    // Defaults to medium setting === 24px
    width: map_get($pnp-icon-size, $size);
    height: map_get($pnp-icon-size, $size);
    vertical-align: middle;
    fill: currentColor;
}

// Mixing to adjust the size in a modifer block or breakpoint
@mixin pnp-svg-icon-modify-size($size: medium) {
    // Defaults to medium setting === 24px
    width: map_get($pnp-icon-size, $size);
    height: map_get($pnp-icon-size, $size);
}
