@use 'sass:meta';

@mixin pnp-link-text {
    @include pnp-print-scheme;
    color: $mixin-link-color;
}

/* Button that looks like a link */
@mixin pnp-link-button {
    @include pnp-print-hide;
    @include pnp-reset-button;
    color: $mixin-link-color;
}

@mixin pnp-button-hover-focus($style: $default-button, $reverse: false) {
    @if ($reverse == true) {
        color: map_get($style, text);
        background-color: map_get($style, bg);
        border-color: map_get($style, border);
    } @else {
        color: map_get($style, hover-text);
        background-color: map_get($style, hover-bg);
        border-color: map_get($style, hover-border);
    }
    outline-offset: -2px;
}

@mixin pnp-template-button($style: $default-button, $reverse: false) {
    @include pnp-reset-button;
    @include pnp-print-hide;
    @include pnp-typeface(text-18, false);
    display: inline-block;
    padding: 8px 16px;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    border: 1px solid transparent;
    border-radius: $rounded-corner-radius;
    outline-offset: 3px;
    cursor: pointer;
    transition: $pnp-button-transition;
    user-select: none;

    @if ($style != no-style) {
        @if ($reverse == true) {
            color: map_get($style, hover-text);
            background-color: map_get($style, hover-bg);
            border-color: map_get($style, hover-border);
        } @else {
            color: map_get($style, text);
            background-color: map_get($style, bg);
            border-color: map_get($style, border);
        }

        &:hover {
            @include pnp-button-hover-focus($style, $reverse);
        }

        &:focus {
            @include pnp-button-hover-focus($style, $reverse);
        }
    }

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }

    &:disabled {
        cursor: not-allowed;
        opacity: 0.7;
    }
}

// Style a link to be the same size as a button, same padding, border ect
@mixin pnp-link-button-size {
    @include pnp-typeface(heading-18, false);
    display: inline-block;
    padding: 8px 16px;
    color: $mixin-link-color;
    text-decoration: none;
    border: 1px solid transparent; // Fake border to match the one the button has

    &:hover,
    &:focus {
        text-decoration: underline;
    }
}

@mixin pnp-button($args...) {
    $default-params: defaultTo(
        (
            reverse: false,
            style: $default-button,
            palette: null,
            variants: (),
            variant: null,
        ),
        $args...
    );

    $palette: map-get($default-params, palette);
    $variants: map-get($default-params, variants);
    $reverse: map-get($default-params, reverse);
    $style: map-get($default-params, style);
    $variant: map-get($default-params, variant);

    @if ($palette != null) {
        @include pnp-template-button(no-style);
        @include pnp-palette-button($palette, $variants, $reverse);
    }
    @if ($variant != null) {
        $button-style: map_get($pnp-buttons, $variant);
        @include pnp-template-button($button-style, $reverse);
    } @else {
        @include pnp-template-button($style, $reverse);
    }
}
