@import '../../collapsible/css/collapsible-mixins';

.autocomplete-concierge {
    position: relative;
    @include pnp-media-breakpoint-up(md) {
        width: auto;
    }

    &__icon {
        @include pnp-svg-icon();
    }

    &__form {
        display: flex;
        border: solid 1px $primary-a;
    }

    &__input {
        @include pnp-typeface(text-16, false);
        @include pnp-custom-input;
        flex-grow: 2;
        max-width: 100%;
        border: 0;

        @include pnp-media-breakpoint-up(sm) {
            @include pnp-typeface(text-18, false);
            min-width: 192px;
            line-height: 30px;
        }
    }

    &__submit {
        @include pnp-button($reverse: true);
        display: inline-flex;
        padding: 12px 16px;
        text-transform: uppercase;
        border: transparent;
    }

    &__results {
        position: absolute;
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        min-width: 232px;
        background: $white;
        outline: none;
        box-shadow: 0 20px 50px 0 rgba(0, 0, 0, 0.15);
    }

    &__section {
        @include pnp-reset-list;
        flex: 1 1 100%;
        padding: 0 22px 22px;

        @include pnp-media-breakpoint-up(md) {
            flex: 1 1 40%;

            &:first-child {
                flex: 1 1 100%;
                border-bottom: solid 1px $grey-20;
            }

            &:nth-child(2) {
                margin-right: 22px;
            }
        }
    }

    &__section-list {
        @include pnp-reset-list;
    }

    &__section-title {
        @include pnp-typeface(text-16-bold, false);
        color: $primary-a;
        text-transform: uppercase;
    }

    &__search-result-item {
        @include pnp-typeface(text-14, false);
        margin: 0 0 28px;
        color: $black;
        cursor: pointer;

        &:hover,
        &:focus,
        &[aria-selected='true'] {
            background: $primary-a-light;
        }

        &--organic {
            @include pnp-typeface(text-16, false);
            margin: 0;
        }
    }

    &__highlight {
        @include pnp-typeface(text-18-bold, false);
    }

    @keyframes loader {
        to {
            transform: rotate(360deg);
        }
    }
    @media screen and (prefers-reduced-motion: reduce) {
        &__loader {
            width: 24px;
            height: 24px;
            margin: 12px 16px;
            animation: none;
            animation-duration: 1000ms;
            animation-timing-function: linear;
            animation-iteration-count: infinite;
        }
    }

    &__loader {
        width: 24px;
        height: 24px;
        margin: 12px 16px;
        animation-name: loader;
        animation-duration: 1000ms;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
    }
}

// Following styles are for modal version of concierge
.autocomplete-wrapper {
    &__action {
        @include pnp-button;
        position: relative;
    }

    &__icon {
        @include pnp-svg-icon();
    }
}

.autocomplete-modal {
    &__header {
        display: flex;
        justify-content: flex-end;

        &-close {
            @include pnp-reset-button;
            margin: 10px;
        }
    }

    &__icon {
        @include pnp-svg-icon();
    }
}

.modal__content.autocomplete-modal__content {
    position: fixed;
    top: 0;
    width: 940px;
    max-width: 100%;
    margin-top: 100px;
    outline: none;

    @include pnp-media-breakpoint-down(sm) {
        width: 100vw;
        height: 100vh;
        margin-top: 0;
    }

    .autocomplete-concierge {
        padding: 0 40px 40px;

        &__results {
            @include pnp-collapse-content();
            @include pnp-scroll-y;
            position: absolute;
            height: 0;
            background-color: transparent;
            box-shadow: none;

            &--open {
                height: 40vh;

                @include pnp-media-breakpoint-down(sm) {
                    height: 100vh;
                }
            }
        }

        &__section {
            padding: 0;

            @include pnp-media-breakpoint-up(md) {
                flex: 1 1 100%;

                &:first-child {
                    border-bottom: 0;
                }
            }
        }
    }
}

.autocomplete-results {
    &__title {
        @include pnp-typeface(text-16-bold, false);
        color: $black;
        text-decoration: none;
    }

    &__metadata {
        @include pnp-link-button;
        @include pnp-typeface(text-16, false);
        display: flex;
        align-items: flex-start;
    }

    &__icon {
        @include pnp-svg-icon();
        flex-shrink: 0;
        margin-right: 10px;
        transform: scale(0.8);
    }
}
