// Common transition options for any primary nav transitions
@mixin primary-nav-transition() {
    transition: background-color 0.25s ease, color 0.25s ease;
    @include pnp-media-prefers-reduced-motion {
        transition: none;
    }
}

// Common hover and focus effect for navigation items and actions
@mixin primary-nav-hover-focus($backgroundColor: $grey-10) {
    background: $backgroundColor;
    outline-offset: -2px;
}

.primary-nav-wrapper {
    @include pnp-print-hide;
    position: relative; // This is the anchor point of the mega-menu
    padding: 24px 0;
    color: $primary-a;
    background-color: $white;

    &--compact {
        .primary-nav-item {
            position: relative;

            &--active,
            &--has-children:hover,
            &--active,
            &--has-children:focus {
                // Arrow connector for the menu
                &::after {
                    position: absolute;
                    bottom: -20px;
                    left: calc(50% - 6px);
                    width: 0;
                    height: 0;
                    border-right: 12px solid transparent;
                    border-bottom: 15px solid $grey-10;
                    border-left: 12px solid transparent;
                    content: '';
                }
            }

            .primary-nav-item__child-menu {
                top: 61px;
                left: var(--menuOffsetNeeded, 0);
            }
        }
    }

    &--overflow {
        .primary-nav__left {
            flex: 1 0 100%;
        }

        .primary-nav__landmark {
            flex: 0 1 80%;
            min-width: 0;
        }

        .primary-nav__right {
            flex: 1 0 auto;
        }

        .primary-nav-item {
            flex-shrink: 0;
        }

        // This is to move mega menu items below overflowing menu.
        .primary-nav-item__child-menu {
            top: 141px;
        }

        .overflow-menu .overflow-menu__icon {
            transform: translateX(10px);
        }
    }
}

.primary-nav {
    @include pnp-max-width-container;

    display: flex;
    flex-wrap: wrap; // Better to wrap than hide controls
    align-items: center;
    justify-content: space-between;
    padding: 0 16px;

    @include pnp-media-breakpoint-up(xl) {
        // If desktop and no nav items are being shown space the logo and actions apart
        &:not(.primary-nav--desktop-burger) {
            justify-content: initial;
        }
    }

    // Hide primary nav if desktop burger is enabled
    &--desktop-burger {
        .primary-nav {
            &__landmark {
                display: none;
            }

            &__left,
            &__right {
                flex: inherit;
            }
        }
    }
    // If a desktop burger is not being shown, hide the menu on desktop breakpoint
    @at-root .primary-nav:not(.primary-nav--desktop-burger) {
        .hamburger-landmark {
            @include pnp-media-breakpoint-up(xl) {
                display: none;
            }
        }
    }

    &__icon {
        @include pnp-svg-icon();
    }

    &__left,
    &__right {
        // Flex growth ratio to center the level 1 navigation
        @include pnp-media-breakpoint-up(xl) {
            display: inline-flex;
            flex: 1 1 0;
            align-items: center;
        }
    }

    // Site logo
    &__logo {
        display: block;

        &-image {
            max-width: 40vw;
            height: 40px;
            object-fit: contain;
            @include pnp-media-breakpoint-up(lg) {
                height: 64px;
            }
        }
    }

    &__site-name {
        @include pnp-typeface(heading-20, false);
        margin-left: 16px;
    }

    // Level 1 navigation list
    &__landmark {
        display: none;
        flex: 6 1 0;
        @include pnp-media-breakpoint-up(xl) {
            display: block;
        }
    }

    &__list {
        @include pnp-reset-list;
        display: flex;
        align-items: center;

        // Alignment options
        &--center {
            justify-content: center;
        }

        &--right {
            justify-content: flex-end;
        }
    }

    // Right side action area for search, hamburger, login ect
    &__right {
        display: flex;
        justify-content: flex-end;
    }

    // Left or right side action buttons / links
    &__action {
        @include pnp-reset-button; // Assumed most 'actions' will be buttons, but this mixin also works on links
        @include primary-nav-transition; // Use the common primary nav background and colour transition
        padding: 12px 16px;
        color: $primary-a;
        background-color: $white;
        cursor: pointer;

        @include pnp-media-breakpoint-down(md) {
            &:not(.primary-nav__action--menu):not(.primary-nav__action--search):not(.primary-nav__action--show-mobile) {
                display: none;
            }
        }

        &:not(:only-child):not(:last-child) {
            margin-right: 12px;
        }

        &:hover,
        &:focus {
            @include primary-nav-hover-focus; // Use common hover & focus effect
        }

        // Make the action look like a button
        &--button {
            @include pnp-button;
        }

        // Add an icon to the button
        &--with-icon {
            display: inline-flex;
            align-items: center;

            svg {
                margin-right: 16px;
            }
        }

        &--icon-only {
            display: inline-block;
        }
    }
}

.primary-nav-icon {
    width: 24px;
    height: 24px;
    fill: currentColor;
}

// Level 1 navigation item
.primary-nav-item {
    display: flex;

    &--active,
    &:hover,
    &:focus-within {
        .primary-nav-item__link {
            @include primary-nav-hover-focus; // Use common hover & focus effect
        }

        .primary-nav-item__link:not(.primary-nav-item__link--home) {
            &::after {
                width: 100%;
                opacity: 1;
            }
        }
    }

    &__icon {
        @include pnp-svg-icon();
    }

    // Link to the level 1 page
    &__link {
        @include pnp-typeface(text-18, false);
        @include primary-nav-transition; // Use the common primary nav background and colour transition

        position: relative;
        display: block;
        padding: 16px 24px;
        color: $primary-a;

        text-decoration: none;
        background-color: $white;

        @at-root .primary-nav-item--has-children & {
            // Add extra right padding to fit the 'has child' chevron
            padding-right: 40px;
        }

        // Colour accent under nav items
        &:not(.primary-nav-item__link--home) {
            &::after {
                position: absolute;
                bottom: -5px;
                left: 50%;
                width: 0%;
                height: 5px;
                background: $primary-a;
                transform: translate(-50%, 0);
                opacity: 0;
                transition: background-color, transform 0.25s ease;
                content: '';

                @include pnp-media-prefers-reduced-motion {
                    transition: none;
                    animation: none;
                }
            }
        }
    }

    // Expander accessibility control to open mega-menu via keyboard
    &__expander-wrapper {
        position: relative; // Relative anchor for the expander icon
        display: flex;
        align-items: center; // Center the icon in the middle of the text

        .primary-nav-item__icon {
            transform: rotate(90deg);
        }
    }

    &__expander {
        @include pnp-reset-button;
        position: absolute;
        right: 10px;
        pointer-events: none;
    }
}

.primary-nav-item__child-menu {
    position: absolute;
    top: 85px;
    left: 0;
    z-index: $mega-menu;
    visibility: hidden;
    opacity: 0;
}

.primary-nav-item:hover .primary-nav-item__child-menu,
.primary-nav-item--active .primary-nav-item__child-menu,
.primary-nav-item:focus .primary-nav-item__child-menu,
.primary-nav-item--active .primary-nav-item__child-menu {
    visibility: visible;
    opacity: 1;
}

.primary-nav__landmark {
    .overflow-menu {
        &--open {
            background-color: $grey-10;
        }

        &__button {
            @include pnp-typeface(text-18, false);

            position: relative;
            display: block;
            padding: 16px 24px;
            color: $primary-a;

            text-decoration: none;
            background-color: $white;

            &:hover,
            &:focus-within {
                @include primary-nav-hover-focus; // Use common hover & focus effect
                @include primary-nav-transition;
            }
        }

        .primary-nav-item__link {
            @include pnp-reset-button();
            @include pnp-typeface(text-16, false);

            display: block;
            padding: 10px;
            color: $primary-a;
            text-decoration: none;
        }
    }
}
