.tabs {
    &--full-width {
        .tab-list__nav {
            display: flex;
        }
    }

    &--center {
        .tab-list__nav {
            display: flex;
            justify-content: center;
        }
    }

    &--left {
        .tab-list__nav {
            display: flex;
            justify-content: flex-start;
        }
    }

    &--right {
        .tab-list__nav {
            display: flex;
            justify-content: flex-end;
        }
    }
}

.tab {
    &__content {
        position: absolute;
        visibility: hidden;

        &--active {
            position: static;
            visibility: visible;
        }
    }

    &__button {
        @include pnp-reset-button;
        position: relative;
        display: inline-flex;
        align-items: center;
        margin: 0 16px;
        padding: 13px 0;
        white-space: nowrap;

        &::after {
            position: absolute;
            bottom: 0;
            display: block;
            width: 100%;
            height: 6px;
            background-color: transparent;
            transition: background-color 0.3s ease-in-out;
            content: '';
            @include pnp-media-prefers-reduced-motion {
                transition: none;
            }
        }

        &:hover,
        &:focus {
            &::after {
                background-color: $grey-20;
            }
        }

        &--active {
            &::after {
                background-color: $primary-a;
            }

            &:hover,
            &:focus {
                &::after {
                    background-color: $primary-a;
                }
            }
        }

        &:first-of-type {
            margin-left: 0;
        }

        &:last-of-type {
            margin-right: 0;
        }
    }

    &__icon {
        @include pnp-svg-icon();
        margin-left: 1em;
    }
}

.tab-list {
    &__nav {
        display: inline-flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        width: auto;
        max-width: 100%;
        border-bottom: 1px solid $grey-20;
    }
}

.overflow-menu {
    .tab__button {
        margin: 0;
        color: $black;
        transition: color 0.3s ease-in-out, text-decoration 0.3s ease-in-out;
        @include pnp-media-prefers-reduced-motion {
            transition: none;
        }

        &:hover,
        &:focus {
            color: $black;
            text-decoration: underline;
        }
    }

    &__wrapper {
        margin-left: 16px;
    }
}

.tabs {
    .tab {
        &__button {
            @include pnp-typeface(text-16-bold);
            margin: 0 0.625rem;
            padding: 1.375rem 2.125rem 1.4375rem;
            color: $white;
            white-space: nowrap;
            text-decoration: none;
            background-color: rgba(0, 0, 0, 0.8);
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
            transition: color, background-color 0.3s ease-in-out;

            &:hover,
            &:focus {
                color: $black;
                background-color: rgba(240, 240, 240, 0.8);

                &::after {
                    display: none;
                }
            }

            &--active {
                color: $black;
                background-color: $grey-10;

                &::after {
                    display: none;
                }
            }

            &--disabled {
                color: $grey-40;
                pointer-events: none;
            }

            .fas,
            .far,
            .fab {
                margin-right: pxToRem(10);
            }
        }
        @media screen and (prefers-reduced-motion: reduce) {
            &__button {
                transition: none;
            }
        }
    }

    .tab-list {
        border-radius: 10px;
    }

    .overflow-menu {
        max-width: 350px;
        padding: pxToRem(20);
        overflow-x: hidden;
        background-color: rgba(218, 218, 218, 0.9);
        border-radius: 10px 0 10px 10px;

        .tab__button {
            width: 100%;
            margin: 0;
            overflow: hidden;
            color: $white;
            text-decoration: none;
            border-radius: 0;
            overflow: hidden;
            text-overflow: ellipsis;
            display: block;

            &:hover,
            &:focus {
                color: $primary-a-light;
                text-decoration: none;
                background-color: $primary-a;
                cursor: pointer;
            }

            &--active {
                background-color: $primary-a;
            }

            &:first-child {
                border-top-left-radius: 10px;
                border-top-right-radius: 10px;
            }

            &:last-child {
                border-bottom-right-radius: 10px;
                border-bottom-left-radius: 10px;
            }
        }
    }
}

.overflow-menu {
    &__button {
        border-radius: 10px;

        &:hover,
        &:focus,
        &--open {
            background-color: rgba(218, 218, 218, 0.9);
        }

        &--open {
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 0;
        }
    }
}
