// Presentation logic specific for Program Finder.
.program-finder {
    .funnelback-search__body {
        width: 100%;

        .navbar {
            margin-bottom: 2rem;
        }
    }

    .hero-banner {
        height: fit-content;
        background-color: transparent;
    }

    .hero-banner-wrapper {
        max-width: 100rem;
    }

    .hero-banner__contents {
        width: 100%;
        padding: 2rem 0;
    }

    .funnelback-search__header {
        @include pnp-typeface(text-18, no-margin);
        color: $white;
        background-color: $black;
    }

    .funnelback-search__filters-button {
        margin-left: auto;
    }

    .navbar .funnelback-facet .facet-group {
        background-color: transparent;
    }

    .listing__items {
        &--card-view {
            .listing-item {
                @include pnp-media-breakpoint-up(xl) {
                    flex: 0 0 calc(25% - pxToRem(20));
                    width: calc(25% - pxToRem(20));
                    min-width: calc(25% - pxToRem(20));
                    max-width: calc(25% - pxToRem(20));
                }
            }
        }
    }
}
