.shortlist-drawer {
    .drawer {
        &__controllers {
            display: flex;
            justify-content: space-between;
            padding: 0 pxToRem(20);
            color: $white;
            background-color: $black;
        }

        &__icon {
            @include pnp-svg-icon(x-large);
        }

        &__close {
            @include pnp-button;
            padding: 0;
            color: $primary-a;
            background-color: transparent;
            border: none;
        }

        &__close:hover,
        &__close:focus {
            background-color: transparent;
        }

        &__content {
            background-color: $grey-10;

            .search-shortlist__hide {
                display: none;
            }
        }

        .listing__items--card-view {
            flex-wrap: nowrap;
            overflow: scroll;

            .flb-cart-box-item {
                margin: 1rem;
            }

            .listing-item {
                width: 25rem;
                min-width: 100%;
                max-width: 100%;
                height: 100%;
            }
        }
    }
}
