.tabs {
    &--full-width {
        .tab-list__nav {
            display: flex;
        }
    }

    &--center {
        .tab-list__nav {
            display: flex;
            justify-content: center;
        }
    }

    &--left {
        .tab-list__nav {
            display: flex;
            justify-content: flex-start;
        }
    }

    &--right {
        .tab-list__nav {
            display: flex;
            justify-content: flex-end;
        }
    }
}

.tab {
    &__content {
        position: absolute;
        visibility: hidden;

        &--active {
            position: static;
            visibility: visible;
        }
    }

    &__button {
        @include pnp-reset-button;
        position: relative;
        display: inline-flex;
        align-items: center;
        margin: 0 16px;
        padding: 13px 0;
        white-space: nowrap;

        &::after {
            position: absolute;
            bottom: 0;
            display: block;
            width: 100%;
            height: 6px;
            background-color: transparent;
            transition: background-color 0.3s ease-in-out;
            content: '';
            @include pnp-media-prefers-reduced-motion {
                transition: none;
            }
        }

        &:hover,
        &:focus {
            &::after {
                background-color: $grey-20;
            }
        }

        &--active {
            &::after {
                background-color: $primary-a;
            }

            &:hover,
            &:focus {
                &::after {
                    background-color: $primary-a;
                }
            }
        }

        &:first-of-type {
            margin-left: 0;
        }

        &:last-of-type {
            margin-right: 0;
        }
    }

    &__icon {
        @include pnp-svg-icon();
        margin-left: 1em;
    }
}

.tab-list {
    &__nav {
        display: inline-flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        width: auto;
        max-width: 100%;
        border-bottom: 1px solid $grey-20;
    }
}

.overflow-menu {
    .tab__button {
        margin: 0;
        color: $black;
        transition: color 0.3s ease-in-out, text-decoration 0.3s ease-in-out;
        @include pnp-media-prefers-reduced-motion {
            transition: none;
        }

        &:hover,
        &:focus {
            color: $black;
            text-decoration: underline;
        }
    }

    &__wrapper {
        margin-left: 16px;
    }
}
