.listing {
    &--linked-item {
        .listing-item {
            padding: 0;
        }
    }

    &__intro--sr-only {
        @include pnp-visuallyhidden;
    }

    @media print {
        .listing-item {
            border: solid 1px $black;
        }
    }
}

.listing-item {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    overflow: hidden;
    color: $black;
    background: $grey-10;
    border-radius: $rounded-corner-radius;

    @include pnp-media-breakpoint-up(lg) {
        flex-direction: row;
    }

    &__link {
        display: flex;
        flex-direction: row;
        width: 100%;
        color: inherit;
        text-decoration: none;
    }

    &__image-wrapper {
        @include pnp-print-hide;
        flex-shrink: 0;
        width: 100%;
        height: 260px;

        @include pnp-media-breakpoint-up(lg) {
            width: 260px;
            height: auto;
        }
    }

    &__icon {
        @include pnp-svg-icon(small);

        &--image {
            width: 100%;
            height: 100%;
        }

        &--document {
            @include pnp-svg-icon-modify-size();
        }

        &--social {
            @include pnp-svg-icon-modify-size();
        }
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;

        @include pnp-media-breakpoint-up(lg) {
            max-width: 260px;
            min-height: 260px;
        }
    }

    &__content {
        flex: 1 1 0;
        width: 100%;
        padding: 20px;
        word-break: break-word;

        @include pnp-media-breakpoint-up(sm) {
            padding: 20px 28px;
        }
    }

    &__title-link {
        display: block;
        color: inherit;
        text-decoration: none;

        &:hover,
        &:focus {
            text-decoration: underline;
        }
    }

    // Header related elements below
    &__header {
        margin-bottom: 16px;
    }

    &__title {
        @include pnp-reset-heading;
        @include pnp-typeface(heading-20-bold, false);
        word-break: break-word;

        @include pnp-media-breakpoint-up(lg) {
            @include pnp-typeface(heading-26-bold, false);
        }
    }

    &__subtitle {
        @include pnp-typeface(text-16, false);
        display: flex;
        align-items: center;
        word-break: break-word;

        @include pnp-media-breakpoint-up(lg) {
            @include pnp-typeface(text-18, false);
        }

        &-link {
            color: inherit;
            text-decoration: none;

            &:hover,
            &:focus {
                text-decoration: underline;
            }
        }

        &--highlight {
            color: $primary-a;
        }

        .listing-item__icon {
            margin-right: 4px;
            color: $primary-a;
        }
    }

    &__subtitle-block {
        display: flex;
        align-items: center;
        color: inherit;

        @include pnp-media-breakpoint-up(sm) {
            margin-right: 16px;
        }

        &--highlight {
            color: $primary-a;
        }

        .listing-item__icon {
            margin-right: 4px;
            color: $primary-a;
        }
    }

    &__date {
        color: $primary-a;
        @include pnp-media-breakpoint-up(lg) {
            position: absolute;
            bottom: 12px;
            left: 12px;
            padding: 12px 20px;
            text-align: center;
            background-color: rgba($grey-10, 0.7);
            backdrop-filter: blur(10px);

            &--top {
                top: 12px;
                bottom: unset;
            }
        }
        @media print {
            position: relative;
            top: 0;
            left: 0;
            display: flex;
            flex-direction: column;
            align-items: start;
            margin: 0;
            padding: 0;
            color: $black;
            background: transparent;
        }
    }

    &__month {
        margin-right: 4px;
        @include pnp-media-breakpoint-up(lg) {
            @include pnp-typeface(text-14-bold, false);
            display: block;
            margin-right: 0;
        }
    }

    &__day {
        @include pnp-media-breakpoint-up(lg) {
            @include pnp-typeface(text-24, false);
            color: $black;
        }
    }

    // Body related elements below
    &__body {
        @include pnp-typeface(text-16, false);
        margin-bottom: 8px;

        @include pnp-media-breakpoint-up(lg) {
            @include pnp-typeface(text-18, false);
        }
    }

    &__summary {
        min-height: 32px;
        margin-bottom: 24px;
    }

    &__tags {
        @include pnp-reset-list;
        display: flex;
        flex-wrap: wrap;
        margin-top: 16px;
        margin-bottom: 16px;
    }

    &__tag {
        @include pnp-typeface(text-14, false);
        margin: 0 8px 8px 0;
        padding: 4px 16px;
        color: $primary-a;
        background-color: $white;
        border-radius: $rounded-corner-radius;
    }

    &__action {
        @include pnp-button;
        @include pnp-typeface(text-14, false);

        @include pnp-media-breakpoint-up(lg) {
            @include pnp-typeface(text-16, false);
        }

        &--right {
            float: right;
        }
    }

    // Footer related elements below
    &__footer {
        display: flex;
        flex-wrap: wrap;
        margin-top: 24px;
        padding-top: 16px;
        border-top: 1px solid $grey-20;
    }

    &__footer-block {
        @include pnp-typeface(text-14, false);
        display: flex;
        align-items: center;
        margin-right: 48px;
        margin-bottom: 4px;
        color: inherit;

        @include pnp-media-breakpoint-up(lg) {
            margin-bottom: 0;
        }

        &--highlight {
            color: $primary-a;
        }

        .listing-item__icon {
            margin-right: 4px;
            color: $primary-a;
        }
    }
}

.listing-item--event {
    @include pnp-media-breakpoint-down(lg) {
        .listing-item__image-wrapper {
            display: none;
        }
    }
    // If the event has no image move the content across the same width as the image
    .listing-item__content {
        margin-left: 260px;

        @include pnp-media-breakpoint-down(lg) {
            margin-left: 0;
        }
    }

    // If it does have an image, dont move it because the image already does that
    .listing-item__image-wrapper + .listing-item__content {
        margin-left: 0;
    }
}

.listing-item--person {
    @include pnp-media-breakpoint-down(lg) {
        .listing-item__image-wrapper {
            position: absolute;
            top: 24px;
            left: 40px;
            width: 80px;
            height: 80px;
        }

        .listing-item__image {
            width: 80px;
            min-height: 80px;
        }

        .listing-item__header {
            display: flex;
            flex-direction: column;
            justify-content: center;
            min-height: 80px;
            margin-left: 104px;
        }

        .listing-item__summary {
            min-height: auto;
        }
    }
    @include pnp-media-breakpoint-down(sm) {
        .listing-item__image-wrapper {
            left: 24px;
        }

        .listing-item__header {
            margin-left: 96px;
        }
    }
}

.listing-item--social-media,
.listing-item--document {
    .listing-item__image-wrapper {
        position: absolute;
        top: 24px;
        left: 24px;
        width: 80px;
        height: 80px;

        @include pnp-media-breakpoint-up(sm) {
            left: 28px;
        }
    }

    .listing-item__image {
        width: 80px;
        min-height: 80px;
    }

    // prettier-ignore
    .listing-item__image-wrapper + .listing-item__content .listing-item__header {
        margin-left: 96px;
    }

    .listing-item__header {
        display: flex;
        flex-direction: column;
        justify-content: center;
        min-height: 80px;
    }

    .listing-item__subtitle {
        @include pnp-typeface(text-14, false);
    }
}

.listing {
    margin-bottom: pxToRem(30);

    &--linked-item {
        .listing-item {
            padding: 0;
        }
    }

    &--list-view {
        width: 100%;
    }
}

.listing-item {
    // Ensure that listing items on mobile take up the full width of the screen.
    margin-right: pxToRem(-10);
    margin-left: pxToRem(-10);
    background-color: $white;
    border-radius: 0;

    @include pnp-media-breakpoint-up(sm) {
        border-radius: 10px;
    }

    @include pnp-media-breakpoint-up(md) {
        // Ensure that listing items on mobile take up the full width of the screen.
        margin-right: 0;
        margin-left: 0;
    }

    &__title-link {
        width: fit-content;
    }

    &__promoted-tag {
        @include pnp-typeface(text-14-bold, no-margin);
        margin: 0 0.5rem 0 0;
        padding: 0.25rem 0.5rem;
        color: $white;
        background-color: $grey-80;
        border-radius: 4px;
    }

    &--background {
        // @include pnp-generate-background-for-colour-map($pnp-primary-colours);
        // @include pnp-generate-background-for-colour-map($pnp-neutral-colours);

        &-shadow {
            box-shadow: $pnp-box-shadow;
        }
    }

    &__icon {
        @include pnp-svg-icon(small);
    }

    /* 
        Auto generate text colours based on colour maps.
    */
    &--color {
        // @include pnp-generate-foreground-for-colour-map($pnp-primary-colours);
        // @include pnp-generate-foreground-for-colour-map($pnp-neutral-colours);
    }

    &__image-wrapper {
        display: block;
        width: 100%;
        height: pxToRem(260);
        overflow: hidden;

        @include pnp-media-breakpoint-up(lg) {
            width: pxToRem(260);
            height: inherit;
            max-height: none;
        }
    }

    &__image {
        width: 100%;
        max-width: none;
        height: 100%;
        max-height: none;
        object-fit: cover;
    }

    &__time {
        margin-right: 0.25rem;
    }

    &__content {
        display: flex;
        flex: 1 1 0;
        flex-direction: column;
        width: 100%;
        padding: 1.75rem;
        word-break: break-word;

        @include pnp-media-breakpoint-up(sm) {
            padding: 2.0625rem 1.9375rem 2.125rem 2.1875rem;
        }
    }

    // Header related elements below
    &__subtitle {
        svg {
            margin-right: pxToRem(5);
        }

        .fab,
        .far,
        .fas {
            margin-right: pxToRem(5);
            color: $primary-a;
        }
    }

    &__subtitle-block {
        display: flex;
        align-items: center;
        color: inherit;

        @include pnp-media-breakpoint-up(sm) {
            margin-right: 1rem;
        }

        svg {
            margin-right: pxToRem(5);
        }
    }

    &__date {
        color: $primary-a;
        @include pnp-media-breakpoint-up(lg) {
            position: absolute;
            bottom: 0.75rem;
            left: 0.75rem;
            padding: 0.75rem 1.25rem;
            text-align: center;
            background-color: rgba($grey-10, 0.7);
            backdrop-filter: blur(10px);

            &--top {
                top: 0.75rem;
                bottom: unset;
            }
        }
    }

    &__tag {
        padding: 0.375rem 0.9375rem;
        color: $primary-a;
        background-color: $grey-10;
        border-radius: 32px;
    }

    &__action {
        @include pnp-reset-heading;
        @include pnp-typeface(text-18-bold);
        margin-top: pxToRem(10);
        padding: 0.625rem 1.125rem 0.6875rem;
        color: $primary-a;
        background-color: inherit;
        border: 0;
    }

    &__action:hover,
    &__action:focus {
        background-color: transparent;
    }

    // Footer related elements below
    &__footer {
        margin-top: auto;
    }

    &__footer-block {
        margin-bottom: pxToRem(10);
        @include pnp-media-breakpoint-up(lg) {
            margin-bottom: 0;
        }

        .far,
        .fas,
        .fab {
            margin-right: pxToRem(10);
            color: $primary-a;
        }
    }
}

/**
    Provide an overlay over video results to mute the img and provide a
    subtle animation to signify that it is a special type of result.
*/
.listing-item--video {
    .listing-item__image-wrapper {
        position: relative;

        &::after {
            position: absolute;
            top: 0;
            left: 0;
            z-index: $video-result-image-overlay;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.4);
            content: '';
        }

        &:hover .listing-item__image,
        &:focus .listing-item__image {
            transform: scale(1.2);
        }

        .listing-item__image {
            transition: transform 0.3s ease;
        }

        @media screen and (prefers-reduced-motion: reduce) {
            .listing-item__image {
                transition: none;
            }
        }
    }
}

// Styling for the card view
.listing__items {
    &--card-view {
        display: flex;
        flex-wrap: wrap;

        .listing-item {
            flex-direction: column;
            width: fill-available;
            min-width: 100%;

            @include pnp-media-breakpoint-up(md) {
                width: calc(50% - pxToRem(20));
                min-width: calc(50% - pxToRem(20));
                max-width: calc(50% - pxToRem(20));
                margin-right: pxToRem(20);
            }

            @include pnp-media-breakpoint-up(xl) {
                flex: 0 0 calc(33.333% - pxToRem(20));
                width: calc(33.333% - pxToRem(20));
                min-width: calc(33.333% - pxToRem(20));
                max-width: calc(33.333% - pxToRem(20));
            }

            &__title {
                @include pnp-media-breakpoint-up(xl) {
                    @include pnp-reset-heading;
                    @include pnp-typeface(heading-20);
                }
            }

            &__body {
                display: flex;
                flex-direction: column;
                height: 100%;
            }

            &__action {
                margin-top: auto;
                padding: 0;
                text-align: left;
            }

            &__image-wrapper {
                display: block;
                width: 100%;
                height: 16.25rem;
            }

            &__footer {
                flex-direction: column;
            }

            &__footer-block {
                @include pnp-media-breakpoint-up(lg) {
                    margin-bottom: 0.625rem;
                }
            }
        }
    }
}

.listing__items {
    .listing-item--social-media,
    .listing-item--document {
        .listing-item__image-wrapper {
            position: absolute;
            top: 1.5rem;
            left: 1.5rem;
            width: 5rem;
            height: 5rem;

            @include pnp-media-breakpoint-up(sm) {
                left: 1.75rem;
            }
        }

        .listing-item__image {
            width: 5rem;
            min-height: 5rem;
        }

        // prettier-ignore
        .listing-item__image-wrapper + .listing-item__content .listing-item__header {
            margin-left: 6rem;
        }

        .listing-item__header {
            display: flex;
            flex-direction: column;
            justify-content: center;
            min-height: 5rem;
        }

        .listing-item__subtitle {
            @include pnp-typeface(text-14, no-margin);
        }
    }
}
