.az-index {
    @include pnp-typeface(text-16, false);
    padding: pxToRem(24);
    text-align: center;
    border: pxToRem(1) solid $grey-40;

    &__list {
        @include pnp-reset-list;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;

        &-item {
            width: pxToRem(45);
            height: pxToRem(45);
            margin: pxToRem(10);
            vertical-align: bottom;
        }
    }

    &__link {
        @include pnp-transition($pnp-standard-transition);
        @include pnp-button($palette: palette-grey);
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        border: pxToRem(1) solid $grey-40;

        &--active {
            background: $grey-10;
        }

        &--disabled {
            opacity: 0.7;

            &:hover,
            &:focus {
                background: none;
                cursor: not-allowed;
            }
        }
    }
}
