.search-history {
    @include pnp-typeface(text-16, false);
    display: flex;

    @include pnp-media-breakpoint-down(sm) {
        flex-direction: column;
    }

    &__container {
        @include pnp-reset-list;

        display: flex;
        flex: 1;
        flex-flow: column wrap;
        align-content: space-between;
        width: auto;
        margin: 10px;
        padding: 10px;
        border: solid 1px $grey-20;
    }

    &__heading {
        @include pnp-reset-heading;
        @include pnp-typeface(heading-20-bold, false);
    }

    &__icon {
        @include pnp-svg-icon();
        width: 20px;
        height: 20px;
        margin-right: 10px;
    }

    &__list {
        @include pnp-reset-list;

        display: flex;
        flex: auto;
        flex-flow: column wrap;
        padding: 20px 0;

        &-item {
            padding: 10px 0;
        }
    }

    &__button-clear {
        @include pnp-button;
        max-width: 150px;
    }
}

.search-history {
    display: flex;

    &__title {
        @include pnp-reset-heading;
        @include pnp-typeface(heading-26-bold);
    }

    &__hide {
        display: inline-flex;
        align-items: center;
        margin-bottom: pxToRem(10);
        padding: 0;
        color: $primary-a;
        text-decoration: underline;
        background-color: transparent;
        border: 0;
        cursor: pointer;

        svg {
            transform: rotate(180deg);
        }
    }

    &__icon {
        @include pnp-svg-icon(small);
    }

    &__item {
        margin: pxToRem(20px);
    }

    &__items {
        @include pnp-media-breakpoint-up(md) {
            display: flex;
            flex-wrap: wrap;
        }
    }

    &__container {
        display: flex;
        flex-direction: column;
        padding: pxToRem(30);
        background-color: $white;
        border-radius: 20px;
        @include pnp-media-breakpoint-up(md) {
            width: 45%;
        }
    }

    &__heading {
        @include pnp-reset-heading;
        @include pnp-typeface(heading-20-bold);
        width: 100%;
        padding-bottom: pxToRem(30);
        border-bottom: 1px solid $grey-20;

        svg {
            margin-right: pxToRem(5);
        }
    }

    &__list {
        padding-left: 0;
        @include pnp-media-breakpoint-up(md) {
            padding-left: inherit;
        }
    }

    &__list-item {
        margin-top: pxToRem(10);
        list-style: none;

        a {
            color: $primary-a;
        }
    }

    &__last-visited {
        @include pnp-typeface(text-14);
        margin-top: pxToRem(5);
        color: $grey-70;
    }

    &__query {
        margin-left: pxToRem(5);
    }

    &__button-clear {
        @include pnp-button;
    }

    &__icon {
        @include pnp-svg-icon();
    }
}
