.aside-nav {
    position: relative;
    @include pnp-media-breakpoint-down(xl) {
        display: none;
    }
}

.aside-nav__list {
    @include pnp-reset-list;
    height: 0;
    overflow: hidden;
    background: $white;
    opacity: 0;

    .aside-nav__toggle + .aside-nav__list {
        display: none;
        visibility: hidden;
    }

    .aside-nav__toggle--is-expanded + .aside-nav__list {
        display: block;
        height: auto;
        overflow: visible;
        visibility: visible;
        opacity: 1;
    }
}

.aside-nav__list--top,
.aside-nav__list--is-expanded {
    height: auto;
    opacity: 1;
}

.aside-nav__icon {
    @include pnp-svg-icon();

    &--section {
        float: left;
        margin: 10px;
        color: $primary-a;
    }

    &--toggle {
        display: block;
        transform: rotate(90deg);
    }
}

.aside-nav__li {
    position: relative;
    padding-left: 0;
}

.aside-nav__link {
    @include pnp-typeface(text-18, false);
    display: block;
    line-height: 26px;
    text-decoration: none;

    &:link,
    &:visited {
        color: $black;
    }

    .aside-nav__inner {
        display: block;
        padding: 10px 44px 10px 10px;

        &:hover,
        &:active,
        &:focus {
            background-color: $grey-10;
        }
    }

    &--is-hierarchy .aside-nav__inner,
    &--is-current .aside-nav__inner {
        @include pnp-typeface(text-18-bold, false);
        background-color: $grey-10;
    }
}

.aside-nav__link--l2:not(.aside-nav__link--is-current),
.aside-nav__link--l2:not(.aside-nav__link--is-hierarchy) {
    border-bottom: solid 1px $grey-20;
}

.aside-nav__li--has-icon {
    .aside-nav__inner--l3 {
        margin-left: 60px;
    }

    .aside-nav__inner--l4 {
        margin-left: 90px;
    }

    .aside-nav__inner--l5 {
        margin-left: 120px;
    }
}

.aside-nav__inner--l3 {
    margin-left: 30px;
}

.aside-nav__inner--l4 {
    margin-left: 60px;
}

.aside-nav__inner--l5 {
    margin-left: 90px;
}

.aside-nav__toggle {
    position: absolute;
    top: 0;
    right: 0;
    display: none;
    padding: 10px;
    background: none;
    border: 0;
    cursor: pointer;

    &--is-expanded {
        .aside-nav__icon--toggle {
            transform: rotate(270deg);
        }
    }

    &--has-children {
        display: block;
    }
}
