.pagination {
    @include pnp-print-hide;
    display: flex;
    justify-content: center;
    width: 100%;
    margin-bottom: 20px;

    &__list {
        @include pnp-reset-list;
        display: none;

        @include pnp-media-breakpoint-up(md) {
            display: flex;
        }

        &--mobile {
            display: flex;

            @include pnp-media-breakpoint-up(md) {
                display: none;
            }
        }
    }

    &__item {
        @include pnp-typeface(text-14, false);
        min-width: 36px;
        min-height: 36px;
        margin: 0 2px;
        color: $primary-a;
        background-color: $grey-10;
        border-radius: $rounded-corner-radius;
        transition: background-color ease-in 0.2s, color ease-in 0.2s;

        @include pnp-media-breakpoint-up(md) {
            @include pnp-typeface(text-18, false);
            min-width: 44px;
            min-height: 44px;
        }

        &:hover,
        &:focus,
        &--current {
            color: $white;
            background-color: $primary-a;
        }

        &--previous {
            .pagination__link {
                padding: 0 12px;
            }

            .pagination__label {
                margin-left: 4px;
            }

            .pagination__icon {
                transform: rotate(180deg);
            }
        }

        &--next {
            .pagination__link {
                padding: 0 12px;
            }

            .pagination__label {
                margin-right: 4px;
            }
        }

        @media (prefers-reduced-motion: reduce) {
            transition: none;
        }
    }

    &__icon {
        @include pnp-svg-icon();
    }

    &__link {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        color: inherit;
        text-decoration: none;
    }
}
