@use 'sass:map';

@mixin pnp-palette-colours($palette) {
    // color can be accessed via currentColor but no such thing exist for the background
    --pnp-palette-bg-color: #{map-get(map-get($pnp-palettes, $palette), bg)};
    --pnp-palette-bg-color-rgb: #{hexToRGB(map-get(map-get($pnp-palettes, $palette), bg))};

    color: map-get(map-get($pnp-palettes, $palette), text);
    background: map-get(map-get($pnp-palettes, $palette), bg);
}

@mixin pnp-palette-button($palette-id, $variants: (), $reverse: false) {
    $pnp-palette: map.deep-merge($pnp-palettes, $variants);

    @if ($reverse == false) {
        color: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), text);
        background: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), bg);
        border-color: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), border);
    } @else {
        color: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), hover-text);
        background: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), hover-bg);
        border-color: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), hover-border);
    }

    @if ($reverse == false) {
        &:hover,
        &:focus,
        &.hover {
            color: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), hover-text);
            background: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), hover-bg);
            border-color: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), hover-border);
        }
    } @else {
        &:hover,
        &:focus,
        &.hover {
            color: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), text);
            background: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), bg);
            border-color: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), border);
        }
    }

    &:disabled {
        color: map-get(map-get(map-get($pnp-palette, $palette-id), buttons), disabled-text);
    }
}
