/*
    Plug and play default colour palette.

*/
//$pnp-main-font-url: 'https://fonts.googleapis.com/css?family=IBM+Plex+Sans&display=swap';
$pnp-main-font-url: 'https://fonts.googleapis.com/css?family=IBM+Plex+Sans:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap"';

$heading-font: sans-serif;
$body-font: 'IBM Plex Sans', sans-serif;

$container-spacing-values: (
    none: 0,
    small: 30px,
    medium: 50px,
    large: 70px,
    x-large: 90px,
    xx-large: 110px
);

$rounded-corner-radius: 0; // 0 = square edges

// Branding Colours
$primary-a: #0964af;
$primary-b: #0b7f66;
$primary-c: null;
$primary-a-light: #b9cbda;
$primary-b-light: #b9d0cb;
$primary-a-superlight: null;
$primary-a-dark: null;

// Colour gradients
$gradient-a: null;
$gradient-a-reverse: null;
$gradient-b: null;
$gradient-b-reverse: null;

// Branding Secondary
$secondary-a: #e0e1dd;
$secondary-b: #3b2c35;
$secondary-c: #2a1f2d;

// Neutrals
$white: #fff;
$grey-10: #f4f4f4;
$grey-20: #dadada;
$grey-40: #aeaeae;
$grey-70: #707070;
$grey-80: #404040;
$black: #262626;
$real-black: #000;

// Utility
$utility-alert: #c83532;
$utility-light: #f6d25a;
$utility-dark: #05345b;
$utility-link: null;
$utility-focus: #005ecc;

// Social
$social-twitter: #1da1f2;
$social-youtube: #f00;
$social-facebook: #4267b2;

// Selection highlighting
$content-selection-highlight: #b3d4fc;

$base-font-scale: 1; // The font scale multiplier (e.g. 2 = 32px/2em/32px)
$base-font-pixel: 16; // Used for font size calculations & conversions
$base-font-colour: $black;

/*  Define easy variables for breakpoints & setting bootstrap breakpoints
    IF YOU ALTER ANY OF THESE YOU MUST UPDATE src/modules/_global/js/screenSize.js
*/
$xs: map_get($pnp-grid-breakpoints, xs);
$sm: map_get($pnp-grid-breakpoints, sm);
$md: map_get($pnp-grid-breakpoints, md);
$lg: map_get($pnp-grid-breakpoints, lg);
$xl: map_get($pnp-grid-breakpoints, xl);
$xxl: map_get($pnp-grid-breakpoints, xxl);

$max-width: $xxl;
$content-max-width: 980px;
$sidebar-width: 300px;
$sidebar-margin: 32px;

$primary-nav-top-height: 70px;
$primary-nav-bottom-height: 50px;
$primary-nav-vertial-width: 220px;
$primary-nav-vertial-collapsed-width: 56px;
$primary-nav-vertical-changeover-breakpoint: md; // Change between desktop and mobile versions

// Define the grid breakpoints so that these will overwrite the bootstrap breakpoints if the above values are changed
// IF YOU ALTER ANY OF THESE YOU MUST UPDATE src/modules/_global/js/screenSize.js
$grid-breakpoints: (
    xs: $xs,
    sm: $sm,
    md: $md,
    lg: $lg,
    xl: $xl,
    xxl: $xxl
);

$pnp-buttons: (
    primary-a: (
        bg: $primary-a,
        text: $white,
        border: $primary-a,
        hover-bg: $white,
        hover-text: $primary-a,
        hover-border: $primary-a,
        disabled-text: $grey-40
    ),
    primary-b: (
        bg: $white,
        text: $primary-a,
        border: $white,
        hover-bg: $primary-a,
        hover-text: $white,
        hover-border: $white,
        disabled-text: $grey-40
    ),
    neutral-grey: (
        bg: $grey-10,
        text: $black,
        border: $grey-10,
        hover-bg: $grey-20,
        hover-text: $black,
        hover-border: $grey-20,
        disabled-text: $grey-40
    ),
    dark-bg: (
        bg: $primary-a,
        text: $white,
        border: $primary-a,
        hover-bg: $primary-c,
        hover-text: $white,
        hover-border: $primary-c
    ),
    light-bg: (
        bg: $white,
        text: $primary-a,
        border: $white,
        hover-bg: $primary-a-superlight,
        hover-text: $primary-c,
        hover-border: $primary-a-superlight
    ),
    dark-ghost: (
        bg: transparent,
        text: $primary-a,
        border: $primary-a,
        hover-bg: transparent,
        hover-text: $primary-c,
        hover-border: $primary-c
    ),
    light-ghost: (
        bg: transparent,
        text: $white,
        border: $white,
        hover-bg: transparent,
        hover-text: $primary-a-light,
        hover-border: $primary-a-light
    ),
    dark-borderless: (
        bg: transparent,
        text: $primary-a,
        border: transparent,
        hover-bg: transparent,
        hover-text: $primary-c,
        hover-border: transparent
    ),
    light-borderless: (
        bg: transparent,
        text: $white,
        border: transparent,
        hover-bg: transparent,
        hover-text: $primary-a-light,
        hover-border: transparent
    ),
    primary-c: (
        bg: $primary-c,
        text: $white,
        border: $primary-c,
        hover-bg: $primary-a,
        hover-text: $white,
        hover-border: $primary-a
    )
);

$pnp-palettes: (
    palette-a: (
        bg: $primary-a,
        text: $white,
        // This can be a reference to the button colouring or the properties can be fully redefined if custom to this palette
        buttons: map_get($pnp-buttons, primary-b)
    ),
    palette-b: (
        bg: $white,
        text: $primary-a,
        buttons: map_get($pnp-buttons, primary-a)
    ),
    palette-grey: (
        bg: $white,
        text: $grey-10,
        buttons: map_get($pnp-buttons, neutral-grey)
    )
);

// Icon sizes used by the template
$pnp-icon-size: (
    x-small: 14px,
    small: 16px,
    medium: 24px,
    large: 32px,
    x-large: 48px,
    xx-large: 64px
);

// Helper variables to make changing mixins easier
// DO NOT DIRECTLY USE THESE, ONLY CHANGE IN THEMES; use via MIXIN
$default-button: map_get($pnp-buttons, primary-a);
$mixin-link-color: $primary-a;

