        margin-bottom: 48px,
        font-weight: 700
    ),
    heading-48-bold: (
        font-size: 48px,
        line-height: 64px,
        margin-bottom: 32px,
        font-weight: 700
    ),
    heading-32-bold: (
        font-size: 32px,
