        font-weight: 700
    )
);

// Default transition values
$pnp-default-transition-type: ease !default;
$pnp-default-transition-duration: 0.5s !default;
$pnp-default-scale-ratio: 1.05 !default;

$pnp-ease-in-out: cubic-bezier(0.645, 0.045, 0.355, 1) !default;

$pnp-button-transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out,
    box-shadow 0.15s ease-in-out !default;

$pnp-standard-transition: border-color 0.3s ease-in-out, background-color 0.3s ease-in-out;

$pnp-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.15);

