/* stylelint-disable selector-no-qualifying-type */

//.custom-form class required on any Custom Form Asset in Matrix to inherit these styles
.custom-form {
    @include pnp-print-hide;

    padding: 16px;

    input[type='number'],
    input[type='text'],
    input[type='search'],
    input[type='email'],
    input[type='file'],
    textarea,
    select {
        @include pnp-typeface(text-18, true);
        @include pnp-custom-input;
        display: inline-block;
        padding: 10px;

        @include pnp-media-breakpoint-up(md) {
            width: 404px;
        }
    }

    select:not([multiple]) {
        @include pnp-custom-select;
        background-color: transparent;
    }

    select[multiple] {
        @include pnp-typeface(text-18, true);
        color: $black;
    }

    input[type='checkbox'] {
        @include pnp-custom-checkbox;
    }

    input[type='radio'] {
        @include pnp-custom-radio-button;
    }

    label {
        @include pnp-typeface(text-18-bold, true);
        @include pnp-custom-label;
        display: initial;
    }

    ::placeholder {
        @include pnp-typeface(text-18, true);
        color: $grey-70;
    }

    //SQ-Form specific
    .sq-form-question.sq-form-question-file-upload {
        input[type='checkbox'] {
            position: unset;
            width: unset;
            height: unset;
            cursor: unset;
            opacity: unset;
        }
    }

    .sq-form-question-datetime {
        .sq-form-question-answer {
            select {
                width: initial;
                margin-right: 10px;
                margin-bottom: 10px;
                margin-left: 10px;
            }
        }
    }

    .sq-form-question-answer {
        ul {
            @include pnp-reset-list;

            li {
                margin-bottom: 0;
            }
        }
    }

    .sq-form-error {
        display: none;
    }

    .sq-form-question-title {
        @include pnp-custom-label;
    }

    .sq-form-question {
        margin-top: 32px;
    }

    .sq-form-question-note,
    .sq-form-required-field-note {
        @include pnp-custom-note;
    }

    .sq-form-question.sq-form-question-error {
        input,
        textarea,
        select {
            color: $utility-alert;
            border-color: $utility-alert;
            outline-color: $utility-alert;
        }

        .sq-form-error {
            @include pnp-typeface(text-14, true);
            display: block;
            margin-top: 3px;
            color: $utility-alert;
            line-height: 18px;
        }
    }

    .sq-form-section {
        margin: 16px 0;
        padding: 16px;
        border: 1px solid $black;

        .sq-form-question {
            margin-top: 0;
        }
    }

    .sq-form-section-title {
        @include pnp-typeface(text-24, false);
        margin-top: 16px;
    }

    button,
    .sq-form-submit {
        @include pnp-button;
    }

    // Make sure parent position is relative.
    .input-with-reset {
        @include pnp-reset-button;
        position: absolute;
        top: calc(50% - 21px);
        right: 60px;
        display: none;
        color: $primary-a;
        background-color: $white;
        cursor: pointer;
    }
}
