@import '../../drawer/css/global';

.search-history-drawer {
    .drawer {
        &__controllers {
            display: flex;
            justify-content: space-between;
            padding: 0 pxToRem(20);
            color: $white;
            background-color: $black;
        }

        &__icon {
            @include pnp-svg-icon(x-large);
        }

        &__close {
            @include pnp-button;
            padding: 0;
            color: $primary-a;
            background-color: transparent;
            border: none;
        }

        &__close:hover,
        &__close:focus {
            background-color: transparent;
        }

        // Hide the back button as drawers provide a close button.
        &__content {
            background-color: $grey-10;

            .search-shortlist__hide {
                display: none;
            }
        }
    }
}
