.overflow-menu {
    position: absolute;
    right: 0;
    display: flex;
    flex-direction: column;
    max-width: 300px;
    max-height: 0;
    padding: 20px 12px;
    background: $grey-20;
    visibility: hidden;
    transition: max-height 0.3s ease-in-out;
    @include pnp-media-prefers-reduced-motion {
        transition: none;
    }

    &--open {
        @include pnp-scroll-y;
        @include pnp-scroll-x;
        z-index: $mega-menu;
        max-height: 640px;
        visibility: visible;
    }
}

.overflow-menu {
    &__button {
        @include pnp-reset-button;
        position: relative;
        display: inline-flex;
        align-items: center;
        margin: 0;
        padding: 13px;
        white-space: nowrap;

        &:hover,
        &:focus,
        &--open {
            background: $grey-20;
        }
    }

    &__icon {
        @include pnp-svg-icon();
        margin: 0;
    }

    &__wrapper {
        position: relative;
        // Note we use display none here as if there is nothing in the overflow menu we don't want it shown to users or screen readers
        display: none;

        &--visible {
            display: block;
        }
    }
}
